		// 		if (err.errMsg === '401') {
		// 			uni.redirectTo({
		// 				url: '../login/login'
		// 			});
		// 		}
		// 	})
		// },
		link: function(e) {
			let that = this;
			let url = e.currentTarget.dataset.url;
			if (url != null && url != "") {
				uni.navigateTo({
					url: url
				});
			} else {
				that.$util.showToast("正在建设中", 'none', 3000);
			}
		},
		linkPartner: function() {
			uni.navigateTo({
				url: '../qrcode/qrcode'
			})
		},
		linkHelp: function() {
			uni.navigateTo({
				url: '../wareslist/wareslist'
			})
		},
		imglink: function(e) {
			let that = this;
			//console.log(e.currentTarget);
			let url = e.currentTarget.dataset.url;
			if (url != null && url != "") {
				uni.navigateTo({
					url: url
				});
			}
		},
		//事件处理函数
		bindViewTap: function() {
			uni.navigateTo({
				url: '../logs/logs'
			})
		},
		clickmap: function(e) {
			console.log(e)
			uni.navigateTo({
				url: '../recySmart/recySmart',
			})
		},
		//设置用户的默认地址
		getDefaultAddr: function(){
			let that = this;
			that.$showLoading(); // 显示遮罩
			that.$request.postToken("/users/address/findDefaultAddress.do",null).then((res) => {
				if (res.data.status === 0) {
					let address = JSON.parse(res.data.results);
					uni.setStorageSync('address', address);
					that.address = address;
				} else {
					uni.removeStorageSync('address');
					that.$util.showToast(res.data.results, 'none', 5000);
				}
			}).catch((err) => {
				console.log(err);
				that.$util.showToast(err, 'none', 5000);
			}).finally(() => {
				that.$hideLoading();
			})
		}
	},
	onLoad() {
		let that = this;
		that.getDefaultAddr(); // 获取默认地址
	},
	onReady() {
		this.myMap = uni.createMapContext("myMap", this);
		this.myMap.moveToLocation();
	},
	onShow() {
		let that = this;
		let address = uni.getStorageSync("address");   // 读取缓存中地址信息
		if (address != null) {
		  that.$data.address = address;
		}
		this.getCustomerInfo();
	}
